.movies-wrapper{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 50px;
    .movie-card{
        border-radius: 15px 15px 0 0;
        overflow: hidden;
        text-decoration: none;
        position: relative;
        max-width: 250px;
        min-width: 250px;
        background: red;
        margin-bottom: 50px;
        box-shadow: 5px 10px 10px #ccc;
        &:hover{
            .card-img{
                transform: scale(1.2);
            }
        }
        .img-holder{
            width: 100%;
            height: 350px;
            overflow: hidden;
            .card-img{
                width: 100%;
                height: 100%;
                transition: all ease 0.5s;
                object-fit: cover;
            }
        }
        .card-rating{
            position: absolute;
            right: 0;
            top: 10px;
            z-index: 5;
            padding: 12px 25px;
            background: red;
            color: white;
            font-weight: bold;
            border-radius: 25px 0 0 25px ;
        }
        .card-info{
            text-decoration: none;
            padding: 15px;
            padding-bottom: 0;
            .card-title{
                color: white;
                font-size: 20px;
            }
            .release-date{
                color: white;
                margin: 5px 0;
            }
        }
    }
}
