.wrapper{
    margin-top: 50px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .left{
        width: 30%;
        img{
            width: 100%;
            object-fit: cover;
        }
    }
    .right{
        width: 65%;
        .date{
            margin: 20px 0;
            display: inline-block;
            color: red;
            font-weight: bold;
        }
        p{
            color: rgb(102, 98, 98);
        }
    }
}