*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    font-family: 'Mulish', sans-serif;
}

.container{
    max-width: 1140px;
    margin: 0 auto;
}

.actors_line{
    width: 100%;
    margin-top: 50px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.title{
    margin: 20px 0;
}

.wrap-similar_films{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

