.similar_movie{
    position: relative;
    width: 250px;
    height: 150px;
    margin-bottom: 25px;
    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    h3{
        position: absolute;
        left: 10px;
        margin: 0 auto;
        bottom: 15px;
        background: red;
        padding: 5px;
        color: white;
        font-size: 15px;
        font-weight: lighter;
    }
}