.header{
    width: 100%;
    background: purple;
    .container{
        padding: 20px 0;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
    }
    .header-logo{
        width: 100px;
        height: auto;   
        img{
            width: 100%;
        }
    }
    .header-links{
        display: flex;
        .header-link{
            margin-right: 20px;
            text-decoration: none;
            color: white;
            font-size: 25px;
        }
    }
    .header-form{
        input{
            background: transparent;
            outline: none;
            border: none;
            border-bottom: 2px white solid;
            padding: 5px 0;
            color: white;
            &::placeholder{
                color: white;
            }
        }
        .header-search-btn{
            outline: none;
            margin-left: 15px;
            padding: 5px 15px;
            border-radius: 25px;
            border: solid 2px white;
            color: white;
            transition: all ease 0.5s;
            background: transparent;
            &:hover{
                background: white;
                color: purple;
            }
        }
    }
}

